
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {httpUrl} from '../../http_url';

export default {
directives: {
TransferDom
},
data () {
return {
show: false,
showMenus: false,
menus: {
menu1: '退出登录'
},
companyName:'',
companyPhone:'',
companyAddress: '',
user_name:'',
user_phone:'',
email:'',
home_adress:'',
person_name1:'',
person_code1:'',
person_name2:'',
person_code2:''
}
},
created(){
this.infoCheck();
this.getData();
},
components: {
XHeader,
PopupHeader,
Actionsheet,
Card,
Group,
Cell
},
methods:{
infoCheck(){
const _this = this;
this.$axios({
method: 'post',
url:httpUrl.informationCheck,
}).then((res)=>{
console.log(res.data)
let data = res.data.retData;
if(res.data.errCode==200){
let personType = data.person;
let dataType = data.data;
if(personType == 0){
this.$vux.alert.show({
title: '错误提示',
content: '未实名认证，请先实名认证',
onHide () {
_this.$router.push('/credit_info_list');
}
})
return
}else if(dataType == 0){
this.$vux.alert.show({
title: '错误提示',
content: '未提交相关资料',
onHide () {
_this.$router.push('/credit_info_list');
}
})
}
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
getData(){
let postData = this.$qs.stringify({
page:3
})
this.$axios({
method: 'post',
url:httpUrl.userInformation,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.companyName = res.data.retData.company_name;
this.companyPhone = res.data.retData.company_phone;
this.companyAddress = res.data.retData.company_adress;
this.user_name = res.data.retData.user_name;
this.user_phone = res.data.retData.user_phone;
this.email = res.data.retData.email;
this.home_adress = res.data.retData.home_adress;
this.person_name1 = res.data.retData.person_name1;
this.person_code1 = res.data.retData.person_code1;
this.person_name2 = res.data.retData.person_name2;
this.person_code2 = res.data.retData.person_code2;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
goHome(){
this.$router.push('/home');
},
LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}

}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
}
}
}



